.catalog {
  &2 {
    padding: 0 0 100px;
  }
  &__pagination {
    margin-top: 45px;
  }
  &__bottom {
    background-image: url("../img/catalog-bg.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    padding: 100px;
    margin-top: 125px;
    &-inner {
      max-width: 463px;
    }
    &-title {
      font-size: 37px;
      line-height: 120%;
      text-transform: uppercase;
      color: #000000;
    }
    &-desc {
      margin-top: 32px;
      p {
        font-weight: 300;
        font-size: 16px;
        line-height: 130%;
        color: #000000;
        &:not(:first-child) {
          margin-top: 15px;
        }
      }
    }
  }
}

@media screen and (max-width: 1150px) {
  .catalog {
    &2 {
      padding: 0 0 90px;
    }
    &__pagination {
      display: none;
    }
    &__bottom {
      padding: 40px 60px;
      margin-top: 90px;
      &-inner {
        max-width: 442px;
      }
      &-title {
        font-size: 34px;
      }
      &-desc {
        p {
          font-size: 14px;
        }
      }
    }
  }
}

@media screen and (max-width: 990px) {
  .catalog {
    &2 {
      padding: 0 0 80px;
    }
    &__bottom {
      padding: 40px;
      &-inner {
        max-width: 409px;
      }
      &-title {
        font-size: 30px;
      }
      &-desc {
        p {
          font-size: 14px;
        }
      }
    }
  }
}

@media screen and (max-width: 765px) {
  .catalog {
    &2 {
      padding: 0 0 50px;
    }
    &__bottom {
      background-image: url("../img/catalog-bg2.webp");
      margin-top: 50px;
    }
  }
}

@media screen and (max-width: 575px) {
  .catalog {
    &2 {
      padding: 0 0 40px;
    }
    &__bottom {
      width: calc(100% + 60px);
      background-image: url("../img/catalog-bg3.webp");
      padding: 25px 25px 206px;
      margin-top: 40px;
      margin-left: -30px;
      &-inner {
        max-width: 100%;
        text-align: center;
      }
      &-title {
        font-size: 24px;
      }
      &-desc {
        p {
          font-size: 13px;
        }
      }
    }
  }
}