.content {
  padding-left: 126px;
}
.sidebar {
  position: fixed;
  z-index: 3;
  top: 0;
  left: 0;
  width: 126px;
  height: 100%;
  background: #8FAAB4;
  overflow-y: auto;
  &::-webkit-scrollbar {
    width: 0;
  }
  &::-webkit-scrollbar-track {
    background: #ffffff;
    border-radius: 3px;
  }
  &::-webkit-scrollbar-thumb {
    background: #000000;
    border-radius: 3px;
  }
  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 50px 15px;
  }
  &__basket {
    position: relative;
    i {
      display: block;
      max-width: 30px;
      img {
        width: 100%;
      }
    }
    span {
      position: absolute;
      top: -8px;
      right: -8px;
      width: 19px;
      height: 19px;
      background: #FFFFFF;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 14px;
      line-height: 120%;
      color: #797979;
    }
  }
  &__search {
    width: 22px;
    padding: 0;
    border: none;
    margin: 0;
    background: transparent;
    margin-top: 35px;
    img {
      width: 100%;
    }
  }
  &__burger {
    width: 35px;
    height: 28px;
    position: relative;
    z-index: 10;
    cursor: pointer;
    margin-top: 125px;
    span {
      position: absolute;
      left: 0;
      width: 100%;
      height: 2px;
      background: #ffffff;
      transition: all 0.5s;
    }
    span:nth-child(1) {
      top: 0;
    }
    span:nth-child(2) {
      top: 50%;
      transform: translate(0, -50%);
    }
    span:nth-child(3) {
      bottom: 0;
    }
    &--active {
      span {
        background: #ffffff;
      }
      span:nth-child(1) {
        top: 13.5px;
        transform: rotate(-45deg);
      }
      span:nth-child(2) {
        display: none;
      }
      span:nth-child(3) {
        bottom: 13.5px;
        transform: rotate(45deg);
      }
    }
  }
  &__btn {
    width: 68.47px;
    height: 270.51px;
    background: #FFFFFF;
    border: 1px solid transparent;
    margin: 65px auto 0;
    font-weight: 300;
    font-size: 18px;
    line-height: 120%;
    text-align: center;
    color: #24292B;
    &:hover {
      background: #c7c7c7;
    }
    span {
      writing-mode: vertical-rl;
      transform: rotate(180deg);
    }
  }
  &__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 75px;
  }
  &__social {
    width: 38px;
    height: 38px;
    background: rgba(217, 217, 217, 0.22);
    border: 1px solid #FFFFFF;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      max-width: 17px;
    }
    &:not(:first-child) {
      margin-top: 17px;
    }
  }
  &__logo {
    display: block;
    width: 87px;
    margin-top: 95px;
    img {
      width: 100%;
    }
  }
  &__menu {
    position: fixed;
    z-index: 3;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(143, 143, 143, 0.56);
    opacity: 0;
    pointer-events: none;
    transition: all 0.5s;
    &--active {
      opacity: 1;
      pointer-events: all;
    }
  }
  &__menu-inner {
    position: absolute;
    top: 146px;
    left: 126px;
    display: flex;
    align-items: stretch;
  }
  &__menu-content {
    min-width: 226px;
    width: 100%;
    background: #FFFFFF;
    box-shadow: 0px 4px 39px rgba(0, 0, 0, 0.15);
    padding: 25px 0;
    position: relative;
    z-index: 1;
  }
  &__link {
    display: block;
    font-weight: 300;
    font-size: 120%;
    line-height: 19px;
    color: #000000;
    &:hover {
      font-weight: 600;
    }
    &:not(:first-child) {
      margin-top: 15px;
    }
  }
  &__connection {
    padding: 0 25px;
  }
  &__email {
    display: flex;
    align-items: center;
    &:hover {
      span {
        color: #8F0041;
      }
    }
    i {
      width: 34px;
      height: 34px;
      background: #8F0041;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-shrink: 0;
      img {
        max-width: 14px;
      }
    }
    span {
      font-weight: 300;
      font-size: 14px;
      line-height: 120%;
      color: #000000;
      margin-left: 14px;
    }
  }
  &__tel {
    display: flex;
    align-items: center;
    margin-top: 12px;
    &-icon {
      width: 34px;
      height: 34px;
      background: #8F0041;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-shrink: 0;
      img {
        max-width: 14px;
      }
    }
    &-desc {
      margin-left: 14px;
      a {
        display: block;
        font-weight: 600;
        font-size: 14px;
        line-height: 120%;
        color: #000000;
      }
      a:hover {
        color: #8F0041;
      }
      a:not(:first-child) {
        margin-top: 7px;
      }
    }
  }
  &__list {
    margin-top: 40px;
  }
  &__item {
    font-weight: 300;
    font-size: 16px;
    line-height: 120%;
    color: #000000;
    cursor: pointer;
    padding: 0 25px;
    &--active {
      font-weight: 600;
    }
    &:hover {
      font-weight: 600;
    }
    &:not(:first-child) {
      margin-top: 15px;
    }
  }
  &__catalog {
    &--active {
      .sidebar__catalog-show {
        font-weight: 600;
      }
    }
    &-show {
      font-weight: 300;
      font-size: 16px;
      line-height: 120%;
      color: #000000;
      cursor: pointer;
      padding: 0 25px;
      &:hover {
        font-weight: 600;
      }
    }
    &-hidden {
      position: absolute;
      top: 0;
      left: 100%;
      min-width: 189px;
      background: #FFFFFF;
      box-shadow: 0px 4px 39px rgba(0, 0, 0, 0.15);
      height: 100%;
      padding: 147px 25px 25px;
      display: none;
      &--active {
        display: block;
      }
    }
  }
}
