.footer {
  background: #ECECEC;
  padding-left: 126px;
  &__top {
    padding: 30px 0 22px;
    border-bottom: 1px solid #D3D3D3;;
  }
  &__top-inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__logo {
    display: block;
    width: 89px;
    img {
      width: 100%;
    }
  }
  &__top-list {
    display: flex;
    align-items: center;
    margin-left: 15px;
    li {
      font-weight: 300;
      font-size: 16px;
      line-height: 120%;
      color: #000000;
      &:hover {
        color: #8f0041;
      }
      &:not(:first-child) {
        margin-left: 39px;
      }
    }
  }
  &__brevis {
    display: flex;
    align-items: center;
    margin-left: 15px;
    &-icon {
      width: 33px;
      img {
        width: 100%;
      }
    }
    &-desc {
      font-weight: 300;
      font-size: 13px;
      line-height: 130%;
      color: #000000;
      margin-left: 20px;
      a {
        &:hover {
          color: #8f0041;
        }
      }
    }
  }
  &__bottom {
    padding: 25px 0 60px;
  }
  &__bottom-inner {
    display: flex;
    justify-content: center;
    margin-left: -176px;
  }
  &__bottom-list {
    &:not(:first-child) {
      margin-left: 130px;
    }
    li {
      font-weight: 300;
      font-size: 16px;
      line-height: 120%;
      color: #000000;
      &:hover {
        color: #8f0041;
      }
      &:not(:first-child) {
        margin-top: 12px;
      }
    }
  }
}

@media screen and (max-width: 1150px) {
  .footer {
    padding-left: 58px;
    &__top {
      padding: 20px 0;
    }
    &__logo {
      width: 69px;
    }
    &__top-list {
      li {
        font-size: 13px;
        &:not(:first-child) {
          margin-left: 29px;
        }
      }
    }
    &__brevis {
      &-icon {
        width: 28px;
      }
      &-desc {
        font-size: 12px;
        margin-left: 12px;
      }
    }
    &__bottom {
      padding: 20px 0 40px;
    }
    &__bottom-inner {
      margin-left: -207px;
    }
    &__bottom-list {
      &:not(:first-child) {
        margin-left: 70px;
      }
      li {
        font-size: 14px;
        &:not(:first-child) {
          margin-top: 10px;
        }
      }
    }
  }
}

@media screen and (max-width: 990px) {
  .footer {
    padding-left: 44px;
    &__logo {
      width: 53px;
    }
    &__top-list {
      li {
        font-size: 12px;
        &:not(:first-child) {
          margin-left: 22px;
        }
      }
    }
    &__brevis {
      &-desc {
        font-size: 11px;
        margin-left: 11px;
      }
    }
    &__bottom-inner {
      margin-left: -187px;
    }
    &__bottom-list {
      &:not(:first-child) {
        margin-left: 60px;
      }
      li {
        font-size: 13px;
        &:not(:first-child) {
          margin-top: 8px;
        }
      }
    }
  }
}

@media screen and (max-width: 765px) {
  .footer {
    padding-left: 0;
    &__logo {
      width: 53px;
    }
    &__bottom-inner {
      margin-left: -110px;
    }
    &__top-list {
      li {
        font-size: 10px;
        &:not(:first-child) {
          margin-left: 17px;
        }
      }
    }
    &__brevis {
      &-icon {
        width: 22px;
      }
      &-desc {
        font-size: 8px;
        margin-left: 5px;
      }
    }
    &__bottom-list {
      &:not(:first-child) {
        margin-left: 60px;
      }
      li {
        font-size: 10px;
        &:not(:first-child) {
          margin-top: 6px;
        }
      }
    }
  }
}

@media screen and (max-width: 575px) {
  .footer {
    position: relative;
    &__top {
      padding: 10px 0;
    }
    &__top-list {
      display: none;
    }
    &__brevis {
      position: absolute;
      left: 15px;
      bottom: 25px;
      margin-left: 0;
    }
    &__bottom {
      padding: 10px 0 100px;
    }
    &__bottom-inner {
      justify-content: space-between;
      margin-left: 0;
    }
    &__bottom-list {
      &:not(:first-child) {
        margin-left: 25px;
      }
    }
  }
}