.btn {
  display: flex;
  justify-content: center;
  align-items: center;
}
.btn-gray {
  background: #6B8A96;
  color: #FFFFFF;
  &:hover {
    background: #7aa1af;
  }
}
.btn-vinous {
  background: #8F0041;
  color: #ffffff;
  &:hover {
    background: #c90160;
  }
}
.swiper-button-prev {
  z-index: 1;
  width: 55px;
  height: 45px;
  background: #FFFFFF;
  border: 1px solid #C7C7C7;
  &:hover {
    background: #8FAAB4;
    svg path {
      fill: #ffffff;
    }
  }
  &:after {
    display: none;
  }
}
.swiper-button-next {
  z-index: 1;
  width: 55px;
  height: 45px;
  background: #FFFFFF;
  border: 1px solid #C7C7C7;
  &:hover {
    background: #8FAAB4;
    svg path {
      fill: #ffffff;
    }
  }
  &:after {
    display: none;
  }
}
.telegram:hover {
  background: #0088cc;
}
.whatsapp:hover {
  background: #2BB741;
}
.instagram:hover {
  background: linear-gradient(115deg, rgb(249, 206, 52), rgb(238, 42, 123), rgb(98, 40, 215));
}
.display2 {
  display: flex;
  align-items: center;
  i {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  span {
    font-size: 50px;
    line-height: 130%;
    font-weight: 400;
    text-transform: uppercase;
    color: #000000;
    margin-left: 15px;
  }
}
.nav {
  padding: 50px 15px;
  &__list {
    li {
      font-size: 20px;
      line-height: 120%;
      &:not(:first-child) {
        margin-top: 15px;
      }
    }
  }
  &__btn {
    width: 266px;
    height: 60px;
    font-size: 20px;
    line-height: 120%;
  }
}
.breadcrumbs {
  padding: 50px 0 40px;
  &__items {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }
  &__item {
    font-weight: 300;
    font-size: 16px;
    line-height: 120%;
    color: #8F8F8F;
    margin-bottom: 10px;
    position: relative;
    &:not(:last-child) {
      margin-right: 10px;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        right: -6px;
        width: 0.5px;
        height: calc(100% - 2px);
        background: #8F8F8F;
        transform: rotate(17deg);
      }
    }
    &:hover {
      color: #000000;
    }
    &--active {
      color: #000000;
    }
  }
}
.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  &__item {
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 300;
    font-size: 16px;
    line-height: 120%;
    color: #A5A5A5;
    border: 1px solid #E2E2E2;
    &:not(:first-child) {
      margin-left: 15px;
    }
    &--active {
      color: #000000;
      border: 1px solid #636363;
    }
    &:hover {
      color: #000000;
      border: 1px solid #636363;
    }
  }
  &__btn {
    width: 30px;
    height: 30px;
    background: #6B8A96;
    display: flex;
    justify-content: center;
    align-items: center;
    &--next {
      margin-left: 15px;
    }
  }
}
.up {
  position: fixed;
  z-index: 3;
  right: 20px;
  bottom: 20px;
  width: 59px;
  height: 59px;
  background: #6B8A96;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s;
  &--active {
    opacity: 1;
    pointer-events: all;
  }
}
.modal {
  background: rgba(143, 143, 143, 0.56);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  overflow-y: auto;
  z-index: 1000;
  opacity: 0;
  pointer-events: none;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    width: 530px;
    max-width: 100%;
    position: relative;
   background: #ffffff;
    padding: 80px 70px;
    margin: 0 auto;
    @media screen and (max-width: 992px) {
      margin: 50px auto;
      max-width: calc(100% - 30px);
    }
  }
  &__close{
    position: absolute;
    top: 20px;
    right: 20px;
    background-color: transparent;
    padding: 0;
    svg {
      width: 20px;
      height: 20px;
    }
  }
  &__title {
    font-family: 'Lato';
    font-weight: 600;
    font-size: 34px;
    line-height: 120%;
    text-align: center;
    color: #000000;
  }
  &__subtitle {
    font-weight: 300;
    font-size: 30px;
    line-height: 120%;
    color: #000000;
    margin-top: 10px;
    text-align: center;
  }
  &__form {
    margin-top: 40px;
    &-group {
      padding: 0;
      margin: 0;
      border: none;
      &:not(:first-child) {
        margin-top: 50px;
      }
      input {
        width: 100%;
        height: 89.25px;
        font-weight: 300;
        font-size: 24px;
        line-height: 120%;
        color: #000000;
        border: 1px solid #B0B0B0;
        background: #ffffff;
        padding: 0 15px 0 45px;
      }
    }
    &-btn {
      width: 100%;
      height: 89px;
      font-weight: 300;
      font-size: 18px;
      line-height: 120%;
      margin-top: 40px;
    }
  }
  &__check {
    display: inline-flex;
    position: relative;
    margin-top: 40px;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      cursor: pointer;
      margin: 0;
      opacity: 0;
      &:checked ~ label:after {
        background-image: url("../img/check.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: 10px 8px;
      }
    }
    label {
      font-weight: 300;
      font-size: 20px;
      line-height: 120%;
      color: #27343F;
      padding-left: 40px;
      display: inline-block;
      text-align: start;
      position: relative;
      a {
        position: relative;
        z-index: 3;
      }
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 25px;
        height: 25px;
        border: 1px solid #B0B0B0;
      }
    }
  }
}

@media screen and (max-width: 1150px) {
  .swiper-button-prev {
    width: 34px;
    height: 28px;
  }
  .swiper-button-next {
    width: 34px;
    height: 28px;
  }
  .display2 {
    span {
      font-size: 34px;
      margin-left: 8px;
    }
    i svg {
      width: 21px;
      height: auto;
    }
  }
  .breadcrumbs {
    padding: 40px 0 30px;
  }
  .modal {
    &__dialog {
      padding: 50px 30px;
    }
    &__title {
      font-size: 28px;
    }
    &__subtitle {
      font-size: 22px;
    }
    &__form {
      &-group {
        &:not(:first-child) {
          margin-top: 25px;
        }
        input {
          height: 60px;
          font-size: 16px;
          padding: 0 15px;
        }
      }
      &-btn {
        height: 60px;
        font-size: 16px;
        margin-top: 25px;
      }
    }
    &__check {
      margin-top: 25px;
      label {
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 990px) {
  .display2 {
    span {
      font-size: 30px;
    }
  }
}

@media screen and (max-width: 765px) {
  .display2 {
    span {
      font-size: 24px;
    }
  }
  .breadcrumbs {
    padding: 30px 0 20px;
    &__item {
      font-size: 13px;
    }
  }
  .up {
    width: 30px;
    height: 30px;
    svg {
      width: 7px;
      height: auto;
    }
  }
  .modal {
    &__dialog {
      padding: 50px 15px;
    }
  }
}