.product {
 &__inner {
  display: flex;
  align-items: stretch;
  margin-top: 50px;
  border-bottom: 1px solid #CCCCCC;
  padding-bottom: 70px;
 }
 &__left {
  background: #FFFFFF;
  border: 1px solid #CCCCCC;
  width: 570px;
  padding: 10px;
  margin-right: 30px;
 }
 &__right {
  width: calc(100% - 600px);
  display: flex;
  justify-content: center;
  flex-direction: column;
  flex-shrink: 0;
 }
 &__img {
  width: 210px;
  margin: auto;
  img {
   width: 100%;
  }
 }
 &__subtitle {
  font-size: 33px;
  line-height: 120%;
  text-transform: uppercase;
  color: #000000;
 }
 &__desc {
  font-size: 16px;
  line-height: 140%;
  font-weight: 300;
  color: #000000;
  border-top: 1px solid #E9E9E9;
  border-bottom: 1px solid #E9E9E9;
  padding: 30px 0;
  margin-top: 30px;
 }
 &__btns {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  margin-top: 38px;
 }
 &__btn {
  width: 100%;
  height: 62px;
  font-size: 18px;
  line-height: 120%;
  text-align: center;
  padding: 0 5px;
  i {
   margin-left: 7px;
  }
 }
}
.info {
 padding: 40px 0 60px;
 &__tabs {
  &-head {
  display: grid;
   grid-template-columns: 1fr 1fr 1fr 1fr;
   gap: 20px;
  }
  &-body {
   margin-top: 45px;
  }
 }
 &__tab {
  height: 62px;
  background: transparent;
  border: 1px solid #A7A7A7;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 300;
  font-size: 16px;
  line-height: 120%;
  text-align: center;
  color: #727272;
  cursor: pointer;
  padding: 0 5px;
  &--active {
   background: #8F0041;
   color: #ffffff;
  }
  &:hover {
   background: #8F0041;
   color: #ffffff;
  }
 }
 &__content {
  display: none;
  &--active {
   display: block;
  }
 }
 &-desc {
  background-image: url("../img/info-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  border: 1px solid #D7D7D7;
  padding: 50px 70px;
  &__text {
   max-width: 882px;
   p {
    font-weight: 300;
    font-size: 16px;
    line-height: 130%;
    color: #000000;
    &:not(:first-child) {
     margin-top: 15px;
    }
   }
  }
  &__list {
   max-width: 770px;
   margin-top: 35px;
   li {
    display: flex;
    &:not(:first-child) {
     margin-top: 25px;
    }
    p {
     font-size: 16px;
     line-height: 130%;
     font-weight: 300;
     color: #000000;
     margin-left: 20px;
    }
   }
  }
 }
 &-ingredient {
  background-image: url("../img/info-bg2.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  border: 1px solid #D7D7D7;
  padding: 50px 70px;
  &__desc {
   max-width: 882px;
   font-size: 16px;
   line-height: 130%;
   font-weight: 300;
   color: #000000;
  }
 }
 &-application {
  background-image: url("../img/info-bg2.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  border: 1px solid #D7D7D7;
  padding: 50px 70px;
  &__desc {
   max-width: 882px;
   font-size: 16px;
   line-height: 130%;
   font-weight: 300;
   color: #000000;
  }
 }
}
.similar-products {
 padding: 60px 0 100px;
 &__slider {
  position: relative;
  margin-top: 45px;
 }
 &__swiper-wrapper {
  align-items: stretch;
 }
 &__slide {
  height: auto;
 }
 .swiper-button-prev {
  left: -88px;
 }
 .swiper-button-next {
  right: -88px;
 }
}

@media screen and (max-width: 1600px) {
 .similar-products {
  .container {
   padding: 0 45px;
  }
  .swiper-button-prev {
   left: -40px;
  }
  .swiper-button-next {
   right: -40px;
  }
 }
}

@media screen and (max-width: 1300px) {
 .product {
  &__left {
   width: 400px;
  }
  &__right {
   width: calc(100% - 430px);
  }
 }
}

@media screen and (max-width: 1150px) {
 .product {
  &__inner {
   padding-bottom: 30px;
   margin-top: 35px;
  }
  &__left {
   width: 335px;
  }
  &__right {
   width: calc(100% - 355px);
  }
  &__img {
   width: 139px;
  }
  &__subtitle {
   font-size: 24px;
  }
  &__desc {
   font-size: 14px;
   padding: 12px 0;
   margin-top: 15px;
  }
  &__btns {
   gap: 15px;
   margin-top: 15px;
  }
  &__btn {
   font-size: 15px;
   height: 55px;
  }
 }
 .info {
  padding: 30px 0 40px;
  &__tabs {
   &-body {
    margin-top: 30px;
   }
  }
  &__tab {
   height: 52px;
   font-size: 14px;
  }
  &-desc {
   padding: 45px;
   &__text {
    p {
     font-size: 14px;
    }
   }
   &__list {
    li {
     i svg {
      width: 30px;
      height: auto;
     }
     p {
      font-size: 14px;
      margin-left: 13px;
     }
    }
   }
  }
  &-ingredient {
   padding: 45px;
   &__desc {
    font-size: 14px;
   }
  }
  &-application {
   padding: 45px;
   &__desc {
    font-size: 14px;
   }
  }
 }
 .similar-products {
  padding-top: 40px;
  &__slider {
   margin-top: 30px;
  }
 }
}

@media screen and (max-width: 990px) {
 .product {
   &__left {
    margin-right: 15px;
   }
  &__right {
   width: calc(100% - 350px);
  }
  &__btn {
   height: 42px;
   font-size: 14px;
  }
 }
 .info {
  padding: 30px 0;
  &__tabs-body {
   margin-top: 25px;
  }
  &__tab {
   font-size: 13px;
  }
  &-desc {
   padding: 30px;
   &__text {
    font-size: 13px;
   }
   &__list {
    li {
     i svg {
      width: 22px;
     }
     p {
      font-size: 13px;
     }
    }
   }
  }
  &-ingredient {
   padding: 30px;
   &__desc {
    font-size: 13px;
   }
  }
 }
 .similar-products {
  padding-top: 30px;
  &__slider {
   margin-top: 25px;
  }
 }
}

@media screen and (max-width: 765px) {
 .product {
  &__inner {
   margin-top: 22px;
  }
  &__left {
   width: 220px;
   padding: 30px;
  }
  &__right {
   width: calc(100% - 235px);
  }
  &__img {
   width: 86px;
  }
  &__subtitle {
   font-size: 18px;
  }
  &__desc {
   font-size: 13px;
   padding: 5px 0;
   margin-top: 12px;
  }
  &__btn {
   height: 39px;
   font-size: 13px;
  }
 }
 .info {
  padding: 20px 0;
  &__tabs-head {
   gap: 15px;
  }
  &__tabs-body {
   margin-top: 20px;
  }
  &-desc {
   padding: 20px;
  }
  &-ingredient {
   padding: 20px;
  }
  &-application {
   padding: 20px;
  }
 }
 .similar-products {
  padding: 20px 0;
  .container {
   padding: 0 30px;
  }
  &__slider {
   margin-top: 15px;
  }
  .swiper-button-prev {
   left: -20px;
  }
  .swiper-button-next {
   right: -20px;
  }
 }
}

@media screen and (max-width: 575px) {
 .product {
  &__title {
   justify-content: center;
  }
  &__inner {
   padding-bottom: 25px;
   display: block;
   max-width: 340px;
   margin: 0 auto;
  }
  &__left {
   width: 100%;
   margin: 0 0 15px 0;
  }
  &__right {
   width: 100%;
  }
 }
 .info {
   &__tabs-head {
    grid-template-columns: 1fr 1fr;
   }
 }
}