.header {
  padding-left: 126px;
}
.header-top {
  padding: 30px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__right {
    display: flex;
    align-items: center;
  }
  &__logo {
    display: block;
    width: 118px;
    img {
      width: 100%;
    }
  }
  &__connection {
    display: flex;
    align-items: center;
  }
  &__email {
    display: inline-flex;
    align-items: center;
    &:hover {
      span {
        color: #8F0041;
      }
    }
    i {
      width: 48px;
      height: 48px;
      background: #8F0041;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-shrink: 0;
      img {
        max-width: 20px;
      }
    }
    span {
      font-weight: 300;
      font-size: 18px;
      line-height: 120%;
      color: #000000;
      margin-left: 14px;
    }
  }
  &__tel {
    display: inline-flex;
    align-items: center;
    margin-left: 70px;
    &-icon {
      width: 48px;
      height: 48px;
      background: #8F0041;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-shrink: 0;
      img {
        max-width: 20px;
      }
    }
    &-desc {
      margin-left: 14px;
      a {
        display: block;
        font-weight: 600;
        font-size: 18px;
        line-height: 120%;
        color: #000000;
      }
      a:hover {
        color: #8F0041;
      }
      a:not(:first-child) {
        margin-top: 10px;
      }
    }
  }
  &__btn {
    width: 251px;
    height: 70px;
    font-weight: 300;
    font-size: 18px;
    line-height: 120%;
    margin-left: 70px;
  }
  &__mob {
    justify-content: space-between;
    align-items: center;
    margin-top: 32px;
    display: none;
    &-right {
      display: flex;
      align-items: center;
    }
    &-socials {
      display: flex;
      align-items: center;
    }
    &-social {
      width: 25px;
      height: 25px;
      background: rgba(217, 217, 217, 0.22);
      border: 1px solid #FFFFFF;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      img {
        max-width: 12px;
      }
      &:not(:first-child) {
        margin-left: 12px;
      }
    }
    &-search {
      width: 13px;
      padding: 0;
      border: none;
      margin: 0;
      background: transparent;
      img {
        width: 100%;
      }
    }
    &-basket {
      position: relative;
      margin-left: 25px;
      i {
        display: block;
        max-width: 15px;
        img {
          width: 100%;
        }
      }
      span {
        position: absolute;
        top: -5px;
        right: -5px;
        width: 12px;
        height: 12px;
        background: #FFFFFF;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 9px;
        line-height: 120%;
        color: #797979;
      }
    }
    &-btn {
      width: 161px;
      height: 34px;
      background: #FFFFFF;
      border: 1px solid #FFFFFF;
      font-weight: 300;
      font-size: 12px;
      line-height: 120%;
      text-align: center;
      color: #24292B;
      margin-left: 45px;
      &:hover {
        background: #6b8a96;
        color: #fff;
      }
    }
  }
}
.header-bottom {
  padding: 35px 0;
  border-top: 1px solid #EDEDED;
  border-bottom: 1px solid #EDEDED;
  &__list {
    display: flex;
    align-items: center;
    margin-left: 40px;
    li:not(:first-child) {
      margin-left: 80px;
    }
    li:last-child {
      &:before {
        content: '';
        position: absolute;
        top: 50%;
        right: -40px;
        transform: translate(0, -50%);
        width: 1px;
        height: 16px;
        background: #CCCCCC;
      }
    }
  }
  &__link {
    font-weight: 300;
    font-size: 18px;
    line-height: 120%;
    color: #000000;
    position: relative;
    &:hover {
      color: #8F0041;
    }
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: -40px;
      transform: translate(0, -50%);
      width: 1px;
      height: 16px;
      background: #CCCCCC;
    }
  }
  &__item {
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: -40px;
      transform: translate(0, -50%);
      width: 1px;
      height: 16px;
      background: #CCCCCC;
    }
    &--active {
      .header-bottom__item-show {
        span {
          color: #8F0041;
        }
        i svg path {
          fill: #8F0041;
        }
      }
      .header-bottom__item-hidden {
        display: block;
      }
    }
    &-show {
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
      &:hover {
        span {
          color: #8F0041;
        }
        i svg path {
          fill: #8F0041;
        }
      }
      span {
        font-weight: 300;
        font-size: 18px;
        line-height: 120%;
        color: #000000;
      }
      i {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-left: 10px;
      }
    }
    &-hidden {
      position: absolute;
      z-index: 2;
      top: 100%;
      left: 0;
      min-width: 200px;
      padding-top: 10px;
      display: none;
    }
    &-link {
      display: block;
      font-weight: 300;
      font-size: 18px;
      line-height: 120%;
      color: #000000;
      &:hover {
        color: #8F0041;
      }
      &:not(:first-child) {
        margin-top: 15px;
      }
    }
  }
  &__hidden-inner {
    background: #FFFFFF;
    box-shadow: 0px 4px 39px rgba(0, 0, 0, 0.15);
    padding: 25px;
  }
}
.header-mob {
  display: none;
  padding: 10px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__logo {
    display: block;
    width: 68px;
    img {
      width: 100%;
    }
  }
  &__burger {
    width: 20px;
    height: 14px;
    position: relative;
    z-index: 10;
    cursor: pointer;
    span {
      position: absolute;
      left: 0;
      width: 100%;
      height: 2px;
      background: #1D1D1D;
      transition: all 0.5s;
    }
    span:nth-child(1) {
      top: 0;
    }
    span:nth-child(2) {
      top: 50%;
      transform: translate(0, -50%);
    }
    span:nth-child(3) {
      bottom: 0;
    }
    &--active {
      span {
        background: #ffffff;
      }
      span:nth-child(1) {
        top: 6px;
        transform: rotate(-45deg);
      }
      span:nth-child(2) {
        display: none;
      }
      span:nth-child(3) {
        bottom: 6px;
        transform: rotate(45deg);
      }
    }
  }
}

@media screen and (max-width: 1150px) {
  .header {
    padding-left: 58px;
  }
  .header-top {
    &__logo {
      width: 73px;
    }
    &__tel {
      margin-left: 45px;
    }
    &__email {
      i {
        width: 28px;
        height: 28px;
        img {
          max-width: 12px;
        }
      }
      span {
        font-size: 15px;
      }
    }
    &__tel {
      &-icon {
        width: 28px;
        height: 28px;
        img {
          max-width: 12px;
        }
      }
      &-desc {
        a {
          font-size: 15px;
        }
      }
    }
    &__btn {
      width: 212px;
      height: 47px;
      margin-left: 45px;
      font-size: 15px;
    }
  }
  .header-bottom {
    &__list {
      margin-left: 35px;
      li {
        font-size: 14px;
        &:after {
          left: -35px;
        }
      }
      li:not(:first-child) {
        margin-left: 70px;
      }
    }
    &__item {
      &-show {
        span {
          font-size: 14px;
        }
      }
    }
  }
}

@media screen and (max-width: 990px) {
  .header {
    padding-left: 44px;
  }
  .header-wrapp {
    position: fixed;
    z-index: 7;
    top: 0;
    left: -105%;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background: #8FAAB4;
    padding: 70px 15px 30px;
    transition: all 0.5s;
    overflow-y: auto;
    &--active {
      left: 0;
    }
    &::-webkit-scrollbar {
      width: 3px;
    }
    &::-webkit-scrollbar-track {
      background: #ffffff;
      border-radius: 3px;
    }
    &::-webkit-scrollbar-thumb {
      background: #000000;
      border-radius: 3px;
    }
  }
  .header-top {
    padding: 0;
    &__inner {
      justify-content: center;
     flex-direction: column;
    }
    &__right {
      flex-direction: column;
    }
    &__logo {
      display: none;
    }
    &__connection {
      flex-direction: column;
      align-items: start;
    }
    &__email {
      span {
        color: #ffffff;
      }
    }
    &__tel {
      margin: 15px 0 0 0;
      &-desc a {
        color: #ffffff;
      }
    }
    &__btn {
      display: none !important;
    }
    &__mob {
      display: flex;
    }
  }
  .header-bottom {
    order: -1;
    margin-bottom: 40px;
    padding: 0;
    border: none;
    &__list {
     flex-direction: column;
      text-align: center;
      margin-left: 0;
      li:not(:first-child) {
        margin: 15px 0 0 0;
      }
      li:last-child {
        &:before {
          display: none;
        }
      }
    }
    &__link {
      display: inline-block;
      color: #ffffff;
      &:after {
        display: none;
      }
    }
    &__item {
      &:after {
        display: none;
      }
      &-show {
        span {
          color: #ffffff;
        }
        i svg path {
          fill: #ffffff;
        }
      }
      &-hidden {
        position: static;
        padding: 10px 0 10px 10px;
      }
      &-link {
        font-size: 14px;
        color: #ffffff;
      }
    }
    &__hidden-inner {
      background: transparent;
      box-shadow: unset;
      padding: 0;
    }
  }
  .header-mob {
    display: block;
    &__burger {
      display: none;
    }
  }
}

@media screen and (max-width: 765px) {
  .header {
    padding-left: 0;
  }
  .header-mob {
    &__burger {
      display: block;
    }
  }
}

@media screen and (max-width: 575px) {
  .header-wrapp {
    padding: 35px 0 15px;
  }
  .header-top {
    &__mob {
      margin-top: 165px;
      &-social {
        &:not(:first-child) {
          margin-left: 7px;
        }
      }
      &-basket {
        margin-left: 10px;
      }
      &-btn {
        margin-left: 10px;
      }
    }
  }
}

@media screen and (max-width: 360px) {
  .header-top {
    &__mob {
      flex-direction: column;
      margin-top: 40px;
      &-right {
        margin-top: 25px;
      }
    }
  }
}